body {
  background-image: url("https://miro.medium.com/max/8284/1*S8UogculWFLr3lVdLn05MA.jpeg");
  padding: 20px;
  margin: 0;
 }
 h1, h2, p, ul, li {
  font-family: sans-serif;
 }
 ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
 }
 ul.header {
  background-color: rgb(24, 3, 31);
  padding: 0;
 }
 ul.header li a {
  color: rgb(190, 139, 238);
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
 }
 .content {
  background-color:  rgb(190, 139, 238);
  padding: 20px;
 }
 .content h2 {
  padding: 0;
  margin: 0;
 }
 .content li {
  margin-bottom: 10px;
 }
 .active {
  background-color: #8118d6;
 }
 #images
 {
   margin-right: 25px;
 }
 label
 {
   font-weight: bold;
 }
 input
 {
   margin:10px;
 }
 select
 {
   margin: 10px;
 }
button {
  background-color:  rgb(190, 139, 238);
  width: 100px;
  height: 35px;
  margin: 10px;
  font-size: large;
  color: black; 
  border: 2px solid #ebdd1b;
}

button:hover {
  background-color: #807917;
  color:  rgb(190, 139, 238);
  border: 2px solid #000000;
}
.map {
  width: 50%;
  height: 10px;
  margin-left: 0;
  margin-top: 30px;
 /* position: relative;
  overflow: hidden;*/
}
